body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@charset "UTF-8";
.App {
  text-align: center; }

p.error::before {
  content: "\274C   "; }

#team-grid {
  margin-left: 30px !important;
  margin-right: 30px !important; }

.team-tile {
  background: #fff !important;
  border-left: 2px solid #002e53;
  text-align: left; }
  .team-tile span {
    color: #000 !important; }
  .team-tile .team-tile-subtitle {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center; }
  .team-tile .team-tile-subtitle, .team-tile .team-tile-subtitle span {
    color: #a3a3a3 !important; }
  .team-tile .team-tile-subtitle span {
    margin-left: 5px;
    margin-top: 5px; }

.team-tile:hover {
  background: #002e53 !important; }
  .team-tile:hover span {
    color: #fff !important; }
  .team-tile:hover .team-tile-subtitle, .team-tile:hover .team-tile-subtitle span {
    color: #fff !important; }

